body {
  background: linear-gradient(to left, #eec9da 0%, #9eacbd 84%);
  margin: 0;
  padding: 30px;
}

.search-form {
  justify-content: center;
  align-items: center;
  height: 20vh;
  display: flex;
  position: relative;
}

.search-form:before {
  content: "";
  opacity: .8;
  z-index: -1;
  position: absolute;
  inset: 0;
}

.search-form input[type="text"] {
  border: none;
  border-radius: 5px;
  width: 300px;
  margin-right: 10px;
  padding: 14px 30px;
  font-size: 16px;
}

.search-form input[type="text"]:focus {
  border-color: #007bff;
  outline: none;
  box-shadow: 0 1px 4px #7d6580, -23px 0 20px -23px #0a1e4d, 23px 0 20px -23px #0a1e4d, inset 0 0 40px #0000001a;
}

.search-form button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #0a1e4d;
  border: none;
  border-radius: 5px;
  padding: 14px 30px;
  font-size: 16px;
}

.search-form button[type="submit"]:hover {
  background: linear-gradient(0deg, #7d6580 5%, #0a1e4d 95%);
  box-shadow: 0 1px 4px #0000004d, -23px 0 20px -23px #0009, 23px 0 20px -23px #0009, inset 0 0 40px #0000001a;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #0a1e4d;
  border: none;
  border-radius: 5px;
  justify-content: center;
  min-width: 180px;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
  padding: 14px 30px;
  font-size: 16px;
  transition: border-color .3s, box-shadow .25s, background-color .35s;
}

.load-more:hover {
  background: linear-gradient(0deg, #7d6580 5%, #0a1e4d 95%);
  box-shadow: 0 1px 4px #0000004d, -23px 0 20px -23px #0009, 23px 0 20px -23px #0009, inset 0 0 40px #0000001a;
}

.gallery {
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  display: grid;
}

.photo-card {
  border: none;
  border-radius: 5px;
  width: 300px;
  margin-right: 10px;
  padding: 14px 30px;
  font-size: 16px;
  transition: border-color .3s, box-shadow .25s;
}

.photo-card:focus {
  border-color: #007bff;
  outline: none;
  box-shadow: 0 1px 4px #7d6580, -23px 0 20px -23px #0a1e4d, 23px 0 20px -23px #0a1e4d, inset 0 0 40px #0000001a;
}

.photo-card:hover {
  box-shadow: 0 1px 4px #7d658099, -23px 0 20px -23px #0a1e4d99, 23px 0 20px -23px #0a1e4d99, inset 0 0 40px #0000000d;
}

.photo-card img {
  border: 1px solid #ccc;
  width: 100%;
  height: auto;
  box-shadow: 0 2px 4px #0000001a;
}

@media (max-width: 767px) {
  .search-form {
    flex-direction: column;
    align-items: stretch;
    height: auto;
  }

  .search-form input[type="text"] {
    width: 100%;
    margin-bottom: 10px;
    margin-right: 0;
  }

  .search-form button[type="submit"] {
    width: 100%;
    margin-right: 0;
  }
}

@media (min-width: 768px) and (max-width: 1023px) {
  .gallery {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  }

  .photo-card {
    width: 250px;
  }
}

@media (min-width: 1024px) {
  .gallery {
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  }
}

